.message-heading-flex-box{
    display: flex;
    justify-content: space-between;
}

.message-heading-flex-box span{
    font-size: 1.2em;
    font-family: 'Ubuntu', sans-serif;
}

.key-heading-only{
    display: flex;
    justify-content: space-between;
}

.key-heading-only span{
    font-size: 1.2em;
    font-family: 'Ubuntu', sans-serif;
}

.level-input-and-heading span{
    text-align: left;
    font-size: 1.2em;
    font-family: 'Ubuntu', sans-serif;
}

.key-and-level-choice-container-main{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 2em;
    margin-bottom: 1em;
}


.encrypt-message-form, .encrypt-key-form, .encrypt-result-form, .encrypt-level-form{
    margin: 0em;
    padding: 0.8em;
    font-size: 1.50em;
    border-width: 0.2em;
    border-color: #CCCCCC;
    background-color: #FFFFFF;
    color: #000000;
    border-style: groove;
    border-radius: 4px;
    box-shadow: 4px 1px 7px rgba(66,66,66,.75);
}
.encrypt-message-form:focus, .encrypt-key-form:focus, .encrypt-result-form:focus, .encrypt-level-form:focus{
    outline:none;
}

.level-heading-only{
    text-align: left;
}

.two-button-container{
    margin: 2em;
    display: flex;
    justify-content: space-evenly;
}

.encrypt-clear-selection{
    text-transform: capitalize;
    width: 10rem;
    height: 2rem;
    align-items: center;
    background: transparent;
    border-color: transparent;
    color: hsl(360, 71%, 66%);
    margin: 0 auto;
    font-size: 1.5rem;
    cursor: pointer;
    transition: all 0.3s linear;
    border-color: hsl(360, 71%, 66%);
    border: solid;
}

.encrypt-clear-selection:hover {
    color: hsl(360, 67%, 44%);
    border-color: hsl(360, 67%, 44%);
    border: solid;
}

.encrypt-main-button{
    text-transform: capitalize;
    width: 10rem;
    height: 2.2rem;
    align-items: center;
    background: transparent;
    border-color: transparent;
    color: hsl(125, 71%, 66%);
    margin: 0 auto;
    font-size: 1.5rem;
    cursor: pointer;
    transition: all 0.3s linear;
    border-color: hsl(125, 71%, 66%);
    border: solid;
}

.encrypt-main-button:hover {
    color: hsl(125, 67%, 44%);
    border-color: hsl(125, 67%, 44%);
    border: solid;
}

.button-options-to-transit{
    display: flex;
    justify-content: space-between;
}

.transit{
    text-align: center;
    text-transform: capitalize;
    width: 10rem;
    height: 2rem;
    align-items: center;
    background: transparent;
    border-color: transparent;
    color: hsl(360, 71%, 66%);
    margin: 1em 1em;
    font-size: 1.5rem;
    cursor: pointer;
    transition: all 0.3s linear;
    border-color: hsl(360, 71%, 66%);
    border: solid;
}

.encrpyt-page-heading{
    margin: 1em 1em;
    text-transform: capitalize;
    font-size: 2em;
    font-weight: 500;
    transition: all 0.1s linear;
}

.transit:hover {
    color: hsl(360, 67%, 44%);
    border-color: hsl(360, 67%, 44%);
    border: solid;
}

.home-button-encrypt{
    display: flex;
    justify-content: space-evenly;
}

.encrypt-to-decrypt{
    border-color: hsl(125, 71%, 66%);
    color: hsl(125, 71%, 66%);
}

.encrypt-to-decrypt:hover{
    color: hsl(125, 67%, 44%);
    border-color: hsl(125, 67%, 44%);
    border: solid;
}

.result-heading{
    display: flex;
    justify-content: space-between;
    font-size: 1.7em;
}

.copy-to-clipboard{
    color: rgb(100, 97, 97);
    cursor: pointer;
    transition: all 0.3s linear;
}

.copy-to-clipboard:hover{
    color: rgb(43, 41, 41);
}