@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 0;
  text-align: center;
  display: grid;
  background: hsl(210, 36%, 96%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.alert-box{
    display: table;
    margin: 1em auto 1em auto;
    padding: 0.25em;
    font-size: 1.3em;
    font-weight: 300;
    color: #721c24;
    background: #f8d7da;
    font-family: 'Ubuntu', sans-serif;
}

h1{
    margin: 1em;
}
.key-container-main-decrypt{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin-top: 2em;
    margin-bottom: 1em;
}
.message-heading-flex-box{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.message-heading-flex-box span{
    font-size: 1.2em;
    font-family: 'Ubuntu', sans-serif;
}

.key-heading-only{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.key-heading-only span{
    font-size: 1.2em;
    font-family: 'Ubuntu', sans-serif;
}

.level-input-and-heading span{
    text-align: left;
    font-size: 1.2em;
    font-family: 'Ubuntu', sans-serif;
}

.key-and-level-choice-container-main{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin-top: 2em;
    margin-bottom: 1em;
}


.encrypt-message-form, .encrypt-key-form, .encrypt-result-form, .encrypt-level-form{
    margin: 0em;
    padding: 0.8em;
    font-size: 1.50em;
    border-width: 0.2em;
    border-color: #CCCCCC;
    background-color: #FFFFFF;
    color: #000000;
    border-style: groove;
    border-radius: 4px;
    box-shadow: 4px 1px 7px rgba(66,66,66,.75);
}
.encrypt-message-form:focus, .encrypt-key-form:focus, .encrypt-result-form:focus, .encrypt-level-form:focus{
    outline:none;
}

.level-heading-only{
    text-align: left;
}

.two-button-container{
    margin: 2em;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
}

.encrypt-clear-selection{
    text-transform: capitalize;
    width: 10rem;
    height: 2rem;
    -webkit-align-items: center;
            align-items: center;
    background: transparent;
    border-color: transparent;
    color: hsl(360, 71%, 66%);
    margin: 0 auto;
    font-size: 1.5rem;
    cursor: pointer;
    transition: all 0.3s linear;
    border-color: hsl(360, 71%, 66%);
    border: solid;
}

.encrypt-clear-selection:hover {
    color: hsl(360, 67%, 44%);
    border-color: hsl(360, 67%, 44%);
    border: solid;
}

.encrypt-main-button{
    text-transform: capitalize;
    width: 10rem;
    height: 2.2rem;
    -webkit-align-items: center;
            align-items: center;
    background: transparent;
    border-color: transparent;
    color: hsl(125, 71%, 66%);
    margin: 0 auto;
    font-size: 1.5rem;
    cursor: pointer;
    transition: all 0.3s linear;
    border-color: hsl(125, 71%, 66%);
    border: solid;
}

.encrypt-main-button:hover {
    color: hsl(125, 67%, 44%);
    border-color: hsl(125, 67%, 44%);
    border: solid;
}

.button-options-to-transit{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.transit{
    text-align: center;
    text-transform: capitalize;
    width: 10rem;
    height: 2rem;
    -webkit-align-items: center;
            align-items: center;
    background: transparent;
    border-color: transparent;
    color: hsl(360, 71%, 66%);
    margin: 1em 1em;
    font-size: 1.5rem;
    cursor: pointer;
    transition: all 0.3s linear;
    border-color: hsl(360, 71%, 66%);
    border: solid;
}

.encrpyt-page-heading{
    margin: 1em 1em;
    text-transform: capitalize;
    font-size: 2em;
    font-weight: 500;
    transition: all 0.1s linear;
}

.transit:hover {
    color: hsl(360, 67%, 44%);
    border-color: hsl(360, 67%, 44%);
    border: solid;
}

.home-button-encrypt{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
}

.encrypt-to-decrypt{
    border-color: hsl(125, 71%, 66%);
    color: hsl(125, 71%, 66%);
}

.encrypt-to-decrypt:hover{
    color: hsl(125, 67%, 44%);
    border-color: hsl(125, 67%, 44%);
    border: solid;
}

.result-heading{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    font-size: 1.7em;
}

.copy-to-clipboard{
    color: rgb(100, 97, 97);
    cursor: pointer;
    transition: all 0.3s linear;
}

.copy-to-clipboard:hover{
    color: rgb(43, 41, 41);
}
h1{
    margin: 0;
    padding: 0;
}

.main-container{
    margin: 0;
    padding: 0;
    height: 100vh;
    width: 100vw;
    background: rgb(255,158,233);
    background: linear-gradient(48deg, rgba(255,158,233,1) 0%, rgba(147,19,123,1) 24%, rgba(116,17,106,1) 76%, rgba(0,212,255,1) 100%);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.home-screen-button{
    width: 15em;
    height: 15em;
    margin: 10em;
    font-family: 'Ubuntu', sans-serif;
    font-weight: 400;
}

.encrypt{
    background: rgb(5,231,0);
background: radial-gradient(circle, rgba(5,231,0,1) 0%, rgba(47,209,0,1) 62%, rgba(21,255,0,1) 100%);     
}

.decrypt{
    background: rgb(0,1,255);
background: radial-gradient(circle, rgba(0,1,255,1) 0%, rgba(6,0,219,1) 62%, rgba(9,0,175,1) 100%);
}

.home-screen-button:hover{
    box-shadow: 3px 3px 4px 1px #b6b8b5;
}

.button-text{
    font-size: 3em;
}
